/**
 * , 
 *  null. 
 *
 * @template T 
 * @param {Array<T>} a 
 * @param {(item: T, index: number) => boolean} each 
 * @returns {(T | null)}
 */
export const find = <T>(a: Array<T>, each: (item: T, index: number) => boolean): T | null => {
    for (let i: number = 0; i < a.length; i++) {
        if (each(a[i], i)) {
            return a[i];
        }
    }

    return null;
};

/**
 * .
 *
 * @template T 
 * @param {Array<T>} a 
 * @param {(item: T, index: number) => boolean} each 
 * @returns {Array<T>}
 */
export const findAll = <T>(a: Array<T>, each: (item: T, index: number) => boolean): Array<T> => {
    let box: Array<T> = [];

    a.forEach((e, i) => {
        if (each(e, i)) {
            box.push(e);
        }
    });

    return box;
};
